<template>
	<div id="customers-detail">
		<!--顶部菜单-->
		<nav-bar class="customers-detail-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				详情
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--滚动-->
		<scroll class="customers-detail-scroller"
				ref="scroller"
				:probe-type=3
		>
		<detail-content :detail-info-obj="DetailInfoObj"/>
		</scroll>
		<customers-detail-btns/>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import DetailContent from 'components/contents/detailcontent/DetailContent'
import Scroll from 'components/common/scroll/Scroll'

import CustomersDetailBtns from './childComps/CustomersDetailBtns'
import {getCustomerDetail} from 'network/customer'

export default {
  name: 'CustomersDetail',
  components:{
	NavBar,
	DetailContent,
	Scroll,
	CustomersDetailBtns
  },
  data(){
	return{
		//数据库取
		DetailInfoObj:{
			telephone:18938333823,
			UnitInfo:{
				customertext:''		//客户
			},
			communicationInfoObj:{

			},
			invoiceInfoObj:{
				shuiHao:'税号',
				bankName:'工商银行',
				account:445154878541545454
			},
		},
		cid:0
	}
  },
  mounted(){
	this.$refs.scroller.refresh();	
	//获取上一个页面传过来的值
	this.cid=this.$route.params.cid?this.$route.params.cid:0
	//console.log(this.cid)
  },
  methods:{
	//返回按钮
	backClick(){
		this.$router.go(-1)
	},
	//获取数据
	getCustomerDetail(cid){
		getCustomerDetail(cid).then(res=>{
			this.DetailInfoObj.UnitInfo=res.unitInfo
			this.DetailInfoObj.communicationInfoObj=res.customerInfo
			this.DetailInfoObj.UnitInfo.customertext=res.customerInfo.customertext
			this.DetailInfoObj.telephone=res.customerInfo.telephone
			this.DetailInfoObj.productneeds=res.productNeedInfo
			//console.log(res)
		})
	}
  },
  created(){
	//获取数据
	this.getCustomerDetail(this.$route.params.id)
  }
}
</script>


